<template>
  <div class="create-box">
      <!-- <div class="create-title">Created</div> -->
       <van-list v-model:loading="loading" :immediate-check="false" v-model:error="error" :error-text="$t('index.The request failed, click Reload')" :loading-text="$t('index.Loading') + '...'" :finished="finished" :finished-text="$t('index.No more')" @load="onLoad">
          <list v-if="list.length" :list="list" :curTime="curTime" @ApproveToken="approveToken" @WithdrawReward="withdrawReward" @LockToken="lockToken"></list>
        </van-list>
      <waitingConfirmationfrom @updateShow="updateShow" :show="showConfirmation" :isWaiting="isWaiting"></waitingConfirmationfrom>
  </div>
</template>
<script>
import List from './List'
import { reactive, toRefs, computed, watch, onMounted } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import config from "@/utils/config";
import BigNumber from 'bignumber.js';
import SeroFactoryAbi from "@/utils/SeroFactory"
import TokenAbi from "@/utils/abi"
import PoolAbi from "@/utils/Pool"
import RouterPathAbi from "@/utils/routerPath"
import { MaxUint256, AddressZero } from "@ethersproject/constants";
import { Multicall } from 'ethereum-multicall';
import waitingConfirmationfrom from '@/components/waitingConfirmation'
import { Toast } from 'vant';


export default {
  components: {
    List,
    waitingConfirmationfrom
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
       showConfirmation: false,
      isWaiting: false,
      btnLoading: false,
      page: 1,
      limit: 6,
      list: [],
      curTime: 0,
      loading: false,
      error: false,
      finished: false,
      initContract: () => {
        data.SeroFactoryContract = new data.web3.eth.Contract(SeroFactoryAbi, config.seroFactoryAddress)
        data.initData()
      },
      updateShow: (value) => {
        data.showConfirmation = value
      },
    
      initData: () => {
        data.getUserCreateProjects()
      },
      onLoad () {
        data.page++
        data.getUserCreateProjects()
      },
      getUserCreateProjects: async () => {
        let projects = await data.SeroFactoryContract.methods.getUserLockProjects(data.address,data.page, data.limit).call()
        console.log(projects, 'projects')
        data.curTime = await data.SeroFactoryContract.methods.getCurTime().call()
        let arr = []
        let promiseArr = []
        for (let index = 0; index < projects.projectArr.length; index++) {
            const item = projects.projectArr[index];
            if(item.lockedToken == AddressZero) {
              break
            }
            const contractCallContext = [
                {
                    reference: 'lockToken',
                    contractAddress: item.lockedToken,
                    abi: TokenAbi,
                    calls: [{ reference: 'decimals', methodName: 'decimals' }, { reference: 'symbol', methodName: 'symbol', methodParameters: []},{ reference: 'allowance', methodName: 'allowance', methodParameters: [data.address,item.lockPool]}, { reference: 'balanceOf', methodName: 'balanceOf', methodParameters: [data.address]}]
                },
                {
                    reference: 'circulationToken',
                    contractAddress: item.circulationToken,
                    abi: TokenAbi,
                    calls: [{ reference: 'decimals', methodName: 'decimals'}, { reference: 'symbol', methodName: 'symbol', methodParameters: [] }]
                },
                {
                    reference: 'lockPool',
                    contractAddress: item.lockPool,
                    abi: PoolAbi,
                   calls: [{ reference: 'getUserData', methodName: 'getUserData', methodParameters: [data.address]}, { reference: 'totalLockAmount', methodName: 'totalLockAmount', methodParameters: [] },{ reference: 'totalBackAmount', methodName: 'totalBackAmount', methodParameters: [] }, { reference: 'rate', methodName: 'rate', methodParameters: [] },{ reference: 'lockTime', methodName: 'lockTime', methodParameters: [] }, { reference: 'canGetNum', methodName: 'getReward', methodParameters: [data.address]}]
                },
                 {
                    reference: 'routerPath',
                    contractAddress: config.RouterPathAddress,
                    abi: RouterPathAbi,
                    calls: [{ reference: 'bestPathAmount', methodName: 'getBestPathAmount', methodParameters: [item.circulationToken, config.UsdtAddress, 1 + '0'.repeat(18)]}]
                }
            ];
            const multicall = new Multicall({ multicallCustomContractAddress: config.MulticallAddress,web3Instance: data.web3, tryAggregate: false });
            promiseArr.push(multicall.call(contractCallContext))
            // const res = await multicall.call(contractCallContext);
            // console.log(res, 'res----')
            // let result = res.results
            // let params = {
            //   lockTokenAddress: item.lockedToken,
            //   lockTokenDecimals:result.lockToken.callsReturnContext[0].returnValues[0],
            //   lockTokenSymbol: result.lockToken.callsReturnContext[1].returnValues[0],
            //   lockTokenAllowance: data.web3.utils.hexToNumberString(result.lockToken.callsReturnContext[2].returnValues[0].hex),
            //   lockTokenBalance: data.web3.utils.hexToNumberString(result.lockToken.callsReturnContext[3].returnValues[0].hex),
            //   circulationTokenAddress: item.circulationToken,
            //   circulationTokenDecimals: result.circulationToken.callsReturnContext[0].returnValues[0],
            //   circulationTokenSympol: result.circulationToken.callsReturnContext[1].returnValues[0],
            //   lockPool: item.lockPool,
            //   reward: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[0].returnValues[0].hex),
            //   totalLockAmount: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[1].returnValues[0].hex),
            //   totalBackAmount: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[2].returnValues[0].hex),
            //   time: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[0].returnValues[1].hex),
            //   stakeAmount: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[0].returnValues[2].hex),
            //   backAmount: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[0].returnValues[3].hex),
            //   lockAmount: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[0].returnValues[4].hex),
            //   rate:data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[3].returnValues[0].hex),
            //   lockTime: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[4].returnValues[0].hex),
            //   enterLockAmount: '',
            //   showDetail: false,
            //   approveBtnLoading: false,
            //   receiveBtnLoading: false
            // }
            // arr.push(params)
        }
          let promiseResult = await Promise.all(promiseArr)
           console.log(promiseResult, 'promiseResult----')
         for(let i = 0; i < promiseResult.length ;i++) {
            let result = promiseResult[i].results
            let params = {
              lockTokenAddress: result.lockToken.originalContractCallContext.contractAddress,
              lockTokenDecimals:result.lockToken.callsReturnContext[0].returnValues[0],
              lockTokenSymbol: result.lockToken.callsReturnContext[1].returnValues[0],
              lockTokenAllowance: data.web3.utils.hexToNumberString(result.lockToken.callsReturnContext[2].returnValues[0].hex),
              lockTokenBalance: data.web3.utils.hexToNumberString(result.lockToken.callsReturnContext[3].returnValues[0].hex),
              circulationTokenAddress: result.circulationToken.originalContractCallContext.contractAddress,
              circulationTokenDecimals: result.circulationToken.callsReturnContext[0].returnValues[0],
              circulationTokenSympol: result.circulationToken.callsReturnContext[1].returnValues[0],
              lockPool: result.lockPool.originalContractCallContext.contractAddress,
              reward: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[0].returnValues[0].hex),
              totalLockAmount: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[1].returnValues[0].hex),
              totalBackAmount: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[2].returnValues[0].hex),
              time: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[0].returnValues[1].hex),
              stakeAmount: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[0].returnValues[2].hex),
              backAmount: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[0].returnValues[3].hex),
              lockAmount: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[0].returnValues[4].hex),
              canGetNum:  data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[5].returnValues[0].hex),
              rate:data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[3].returnValues[0].hex),
              lockTime: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[4].returnValues[0].hex),
              enterLockAmount: '',
              showDetail: false,
              approveBtnLoading: false,
              receiveBtnLoading: false,
              price: data.web3.utils.hexToNumberString(result.routerPath.callsReturnContext[0].returnValues[0].hex)
            }
            arr.push(params)
         }
          if (data.page === 1) {
            data.list = arr
          } else {
            data.list.push(...arr)
          }
          data.loading = false
          data.finished = !(Math.ceil(projects.total / data.limit) > data.page)
      }, 
      updateData: async(item, index) => {
        console.log(item.circulationTokenAddress, 'item.circulationTokenAddress=---------')
        const contractCallContext = [
                {
                    reference: 'lockToken',
                    contractAddress: item.lockTokenAddress,
                    abi: TokenAbi,
                    calls: [{ reference: 'decimals', methodName: 'decimals' }, { reference: 'symbol', methodName: 'symbol', methodParameters: []},{ reference: 'allowance', methodName: 'allowance', methodParameters: [data.address,item.lockPool]}, { reference: 'balanceOf', methodName: 'balanceOf', methodParameters: [data.address]}]
                },
                {
                    reference: 'circulationToken',
                    contractAddress: item.circulationTokenAddress,
                    abi: TokenAbi,
                    calls: [{ reference: 'decimals', methodName: 'decimals'}, { reference: 'symbol', methodName: 'symbol', methodParameters: [] }]
                },
                {
                    reference: 'lockPool',
                    contractAddress: item.lockPool,
                    abi: PoolAbi,
                    calls: [{ reference: 'getUserData', methodName: 'getUserData', methodParameters: [data.address]}, { reference: 'totalLockAmount', methodName: 'totalLockAmount', methodParameters: [] },{ reference: 'totalBackAmount', methodName: 'totalBackAmount', methodParameters: [] }, { reference: 'rate', methodName: 'rate', methodParameters: [] },{ reference: 'lockTime', methodName: 'lockTime', methodParameters: [] }, { reference: 'canGetNum', methodName: 'getReward', methodParameters: [data.address]}]
                },
                {
                    reference: 'routerPath',
                    contractAddress: config.RouterPathAddress,
                    abi: RouterPathAbi,
                    calls: [{ reference: 'bestPathAmount', methodName: 'getBestPathAmount', methodParameters: [item.circulationTokenAddress, config.UsdtAddress, 1 + '0'.repeat(18)]}]
                }
            ];
            const multicall = new Multicall({ multicallCustomContractAddress: config.MulticallAddress,web3Instance: data.web3, tryAggregate: false });
            const res = await multicall.call(contractCallContext);
            console.log(res, 'res----')
            let result = res.results
            let params = {
              lockTokenAddress: item.lockedToken,
              lockTokenDecimals:result.lockToken.callsReturnContext[0].returnValues[0],
              lockTokenSymbol: result.lockToken.callsReturnContext[1].returnValues[0],
              lockTokenAllowance: data.web3.utils.hexToNumberString(result.lockToken.callsReturnContext[2].returnValues[0].hex),
              lockTokenBalance: data.web3.utils.hexToNumberString(result.lockToken.callsReturnContext[3].returnValues[0].hex),
              circulationTokenAddress: item.circulationTokenAddress,
              circulationTokenDecimals: result.circulationToken.callsReturnContext[0].returnValues[0],
              circulationTokenSympol: result.circulationToken.callsReturnContext[1].returnValues[0],
              lockPool: item.lockPool,
              reward: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[0].returnValues[0].hex),
              totalLockAmount: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[1].returnValues[0].hex),
              totalBackAmount: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[2].returnValues[0].hex),
              time: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[0].returnValues[1].hex), 
              stakeAmount: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[0].returnValues[2].hex),
              backAmount: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[0].returnValues[3].hex),
              lockAmount: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[0].returnValues[4].hex),
               canGetNum:  data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[5].returnValues[0].hex),
              rate:data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[3].returnValues[0].hex),
              lockTime: data.web3.utils.hexToNumberString(result.lockPool.callsReturnContext[4].returnValues[0].hex),
              enterLockAmount: '',
              showDetail: true,
              approveBtnLoading: false,
              receiveBtnLoading: false,
              price: data.web3.utils.hexToNumberString(result.routerPath.callsReturnContext[0].returnValues[0].hex)
            }
            data.list[index] = params
      },
      lockToken: async (item, index) => {
        if(!item.enterLockAmount) {
          Toast('Please enter the lock-up amount')
          return
        }
        data.list[index].approveBtnLoading = true
        try {
         let poolContract = new data.web3.eth.Contract(PoolAbi, item.lockPool)
         let amount = new BigNumber(item.enterLockAmount).multipliedBy(10**item.lockTokenDecimals).toFixed()
         poolContract.methods.lockToken(amount).send({from: data.address})
         .on('transactionHash', (hash) => {
              // console.log(hash)
              // store.commit({

              // })
              data.showConfirmation = false
              data.isWaiting = false
              data.showConfirmation = true
           })
          // .on('confirmation', (confirmationNumber, receipt) => {
          //     console.log(2222)
          //     console.log(confirmationNumber, receipt)
          //   })
          .on('receipt', async (receipt) => {
             data.list[index].approveBtnLoading = false
        //  Toast('Success')
             await data.updateData(item, index)
             store.commit('updateResult', {
               hash: receipt.transactionHash,
               time: new Date().getTime(),
               desc: t('index.Lock token'),
               isShow: true
             })
          })
           .on('error', () => {
              data.list[index].approveBtnLoading = false
           })
        } catch (error) {
          Toast(t('index.Fail'))
          data.list[index].approveBtnLoading = false
        } 
      },
      approveToken: async (item, index) => {
         data.list[index].approveBtnLoading = true
        try {
         let tokenContract = new data.web3.eth.Contract(TokenAbi, item.lockTokenAddress)
         await tokenContract.methods.approve(item.lockPool, MaxUint256).send({from: data.address})
         let allowance = await tokenContract.methods.allowance(data.address, item.lockPool).call()
         data.list[index].lockTokenAllowance = allowance
           data.list[index].approveBtnLoading = false
         Toast(t('index.Success'))
        } catch (error) {
          Toast(t('index.Fail'))
          data.list[index].approveBtnLoading = false
        } 
      },
      withdrawReward:  async (item, index) => {
        if(Number(item.reward) <= 0) {
          Toast(t('index.No income to receive'))
          return
        }
        data.curTime = await data.SeroFactoryContract.methods.getCurTime().call()
        if(Number(Number(data.curTime) < Number(item.time) + Number(item.lockTime))) {
          Toast(t('index.The lock-up time has not expired'))
          return
        }
        data.list[index].receiveBtnLoading = true
        try {
         let poolContract = new data.web3.eth.Contract(PoolAbi, item.lockPool)
         poolContract.methods.withdrawReward().send({from: data.address})
          .on('transactionHash', (hash) => {
              // console.log(hash)
              // store.commit({

              // })
              data.showConfirmation = false
              data.isWaiting = false
              data.showConfirmation = true
           })
          // .on('confirmation', (confirmationNumber, receipt) => {
          //     console.log(2222)
          //     console.log(confirmationNumber, receipt)
          //   })
          .on('receipt', async (receipt) => {
             data.list[index].receiveBtnLoading = false
             await data.updateData(item, index)
             store.commit('updateResult', {
               hash: receipt.transactionHash,
               time: new Date().getTime(),
               desc: t('index.Withdraw reward'),
               isShow: true
             })
          })
          .on('error', () => {
             data.list[index].receiveBtnLoading = false
           })
        } catch (error) {
          Toast(t('index.Fail'))
          data.list[index].receiveBtnLoading = false
        } 
      }
    })
    if (data.address) {
      data.initContract()
    }
    onMounted(() => {
    })
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>

<style lang="less" scoped>

.create-title{
  font-size: 16px;
  font-weight: 700;
  color: #1FC7D4;
  margin-bottom: 15px;
  margin-top: -6px;
}
</style>

