<template>

 <div class="list-box">
            <div class="item" v-for="(item, index) in list" :key="item.circulationTokenAddress">
               <div class="item-top-content">
                      <div class="top flex-between">
                 <div class="left flex-middle">
                    <img :src="$tokenLogo(item.circulationTokenAddress)" alt="" @error="$tokenDefault">
                    <span>{{item.circulationTokenSympol}} {{$t('index.Sero contract')}}</span>
                 </div>
                 <div class="right">
                    <div class="share-box flex-middle copy-btn" :data-clipboard-text="config.SeroUser + '/' +item.circulationTokenAddress">
                      <span>{{$t('index.Share')}}</span>
                      <img src="@/assets/images/share_img.png" alt="">
                    </div>
                    <div class="hide-box flex-middle" @click="item.showDetail = !item.showDetail">
                       <img src="@/assets/images/top.png" alt="" v-if="item.showDetail">
                      <img src="@/assets/images/bottom.png" alt="" v-else>
                    </div>
                 </div>
                </div>
               </div>
               <div class="item-bottom-content" v-show="item.showDetail">
                 <div class="pool-address-info">
                   <div class="info-title flex-middle">
                     <span>{{item.circulationTokenSympol}} {{$t('index.SERO contract address')}}</span>
                     <!-- <img src="@/assets/images/bottom.png" alt=""> -->
                   </div>
                   <div class="pool-address">
                     <span>{{item.lockPool ? item.lockPool.substring(0,12) + '......' + item.lockPool.substring(item.lockPool.length - 12, item.lockPool.length) : ''}}</span>
                      <img class="copy-btn" :data-clipboard-text="item.lockPool"  src="@/assets/images/copy_icon.png" alt="">
                   </div>
                 </div>
                 <!-- <div class="pool-top flex-between">
                    <div class="top-left">Total Locked <span>{{Number(item.totalLockAmount/10**18).toFixed(2)}}{{item.lockTokenSymbol}}</span></div>
                    <div class="top-right flex-middle">
                        <span>share</span>
                        <img src="@/assets/images/share.png" alt="">
                    </div>
                 </div> -->
              
                <div class="lock-input">
                  <div class="row flex-between" style="margin-bottom: 7px">
                    <div class="name">{{$t('index.Swap')}}</div>
                    <div class="value">{{$t('index.balance')}}: {{new BigNumber(item.lockTokenBalance/10**item.lockTokenDecimals).toFixed(4,1)}} {{item.lockTokenSymbol}}({{$t('index.original')}})</div>
                    </div>
                    <van-cell-group title="">
                        <van-field v-model="item.enterLockAmount" :placeholder="$t('index.Please enter quantity')" />
                    </van-cell-group>
                     <div class="row flex-between">
                    <div class="name">{{$t('index.Expected to be locked')}}</div>
                    <div class="value">{{Number(item.enterLockAmount) > 0 ? item.enterLockAmount: '---'}} {{item.circulationTokenSympol}}({{$t('index.circulating')}})</div>
                 </div>
                 
                </div>
                   <div class="primary-btn-box">
                    <van-button type="primary" size="large" :loading="item.approveBtnLoading" :loading-text="$t('index.Lock') + '...'" v-if="Number(item.lockTokenAllowance) > 0 && Number(item.lockTokenAllowance) >= Number(item.enterLockAmount)" @click="lockToken(item, index)" block>{{$t('index.Lock')}}</van-button>
                    <van-button type="primary" size="large" :loading="item.approveBtnLoading" :loading-text="$t('index.Approve') + '...'" v-else block @click="approveToken(item, index)">{{$t('index.Approve')}} {{item.lockTokenSymbol}}</van-button>
                </div>
                <div class="line"></div>
                <div class="receive-box">
                  <!-- <div class="row flex-between">
                    <div class="name">I locked:</div>
                    <div class="value">{{Number(item.lockAmount/10**18).toFixed(2)}}{{item.lockTokenSymbol}}</div>
                 </div>
                  <div class="row flex-between">
                    <div class="name">ratio:</div>
                    <div class="value">1 {{item.lockTokenSymbol}}:1 {{item.circulationTokenSympol}}</div>
                 </div> -->
                 <div class="price-info flex-between">
                   <div class="left flex-item">
                     <div class="desc">{{item.circulationTokenSympol}}({{$t('index.circulating')}}) {{$t('index.price')}}</div>
                     <div class="price">
                       ${{new BigNumber(item.price/10**18).toFixed(4,1)}}
                     </div>
                     
                   </div>
                    <van-button style="width: 100px" type="primary" size="small" block @click="toTrade(item.circulationTokenAddress)">{{$t('index.To trade')}}</van-button>
                 </div>
                    <div class="row flex-between">
                    <div class="name">{{$t('index.My contract locks')}}:</div>
                    <div class="value">{{new BigNumber((item.lockAmount-item.backAmount)/10**18).toFixed(4,1)}} {{item.circulationTokenSympol}}</div>
                 </div>
                  <div class="row flex-between">
                    <div class="name">{{$t('index.My contract unlocks')}}:</div>
                    <div class="value">{{new BigNumber(item.canGetNum/10**18).toFixed(4,1)}} {{item.circulationTokenSympol}}</div>
                 </div>
                </div>
                  
                 <div class="primary-btn-box">
                    <van-button type="primary" :loading="item.receiveBtnLoading" :disabled="Number(item.canGetNum)<=0" :loading-text="$t('index.Receive')+ '...'"  size="large" block @click="withdrawReward(item, index)">{{$t('index.Receive')}}</van-button>
                </div>

                 <div class="countdown-box" v-if="Number(item.lockAmount) > 0 && Number(item.reward) > 0">
                   <!-- <div class="countdown-title">Buy amount:{{Number(item.reward/10**18).toFixed(2)}} {{item.circulationTokenSympol}}</div> -->
                    <div class="countdown-text-box">
                       <div class="countdown-text">
                            <van-count-down :time="(Number(item.lockTime) + Number(item.time) - curTime) * 1000" format="HH:mm:ss"/>
                        </div>
                   </div>
                 
                   <div class="countdown-tip">
                    <!-- {{$t('index.After that, you can get to receive', {num: item.rate/100})}} {{Number(item.reward*item.rate/10000/10**18).toFixed(2)}} {{item.circulationTokenSympol}} -->
                    {{$t('index.Afterwards', {num: Number(item.reward*item.rate/10000/10**18).toFixed(2), sympol: item.circulationTokenSympol})}}
                   </div>
                 </div>

                 <div class="view-record flex-middle" @click="viewRecord(item.lockPool)">
                   <span class="flex-item">{{$t('index.SERO contract record')}}</span>
                   <span>{{$t('index.View')}}</span>
                   <img src="@/assets/images/arrow_right.png" alt="">
                 </div>
               </div>
            </div>
          </div>
</template>

<script>
import { Toast } from 'vant';
import { reactive, toRefs, onMounted } from "vue";
import Clipboard from 'clipboard'
import config from "@/utils/config";
import BigNumber from 'bignumber.js';
import { useI18n } from "vue-i18n"

export default {
  props: {
    list: {
      type: Array,
       default: () => {
         return []
       }
     },
     curTime: {
        type: Number,
        default: 0
     }
    },
  setup (props,{ emit }) {
     const { t } = useI18n()
     const data = reactive({
      BigNumber: BigNumber,
      config: config,
      approveToken: (item, index) => {
        emit('ApproveToken', item, index)
      },
      withdrawReward: (item, index) =>{
        emit('WithdrawReward', item, index)
      },
      lockToken: (item, index) => {
        emit('LockToken', item, index)
      },
      toTrade: (token) => {
        window.location.href = `${config.tradeSite}?inputCurrency=${config.UsdtAddress}&outputCurrency=${token}`
      },
      viewRecord: (pool) => {
        window.location.href = `${config.explore}/address/${pool}`
      }
     })
     onMounted(() => {
      var clipboard = new Clipboard('.copy-btn')
      clipboard.on('success', function (e) {
        Toast(t('index.copy success'))
        e.clearSelection()
      })
      clipboard.on('error', function () {
        Toast(t('index.copy fail'))
      })
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>

