  font-size: 16px;
  font-weight: 700;
  color: #280D5F;
  padding: 15px 0;
  img{
    width: 7px;
    margin-left: 6px;
  }
}
.line{
  height: 1px;
  background: #EEEAF4;
  margin: 18px 0;
}
.list-box{
    background: #fff;
    border-radius: 24px;
    border: 1px solid #E7E3EB;
    overflow: hidden;
    margin: 0px 0px;
    .item{
       border-top: 1px solid #E7E3EB;
       &:first-child{
        border: none;
       }
      .item-top-content{
        padding:13px;
      }
      .top{
        padding: 10px 0;
        .left{
          img{
            width: 20px;
          }
          span{
            color: var(--primary-color);
            font-size: 13px;
            font-weight: 600;
            margin-left: 6px;
          } 
        }
        .right{
          .hide-box{
            flex-direction: row-reverse;
          span{
            font-size: 11px;
            color: #2FCBD6;
            font-weight: 600;
          }
          img{
            width: 12px;
            margin-left: 4px;
          }
        }
        .share-box{
          margin-bottom: 15px;
          span{
            font-size: 14px;
            font-weight: 700;
            color: #1EC7D3;
            margin-right: 5px;
          }
          img{
            width: 18px;
          }
        }
        }
      }
     
      .desc-box{
        .desc-item{
          .name{
            color: #7A6EAA;
            font-size: 12px;
            font-weight: 600;
            margin-bottom: 4px;
          }
          .value{
             color: var(--primary-color);
            font-size: 14px;
          }
        }
      
      }
      .item-bottom-content{
        padding: 15px 13px;
        background: #F6F6F6;
        border-top: 1px solid #E7E3EB;
        .pool-top{
          border: 1px solid #EEEAF4;
          border-radius: 16px;
          height: 46px;
          padding: 0 16px;
          margin-bottom: 12px;
          .top-left{
            color: #7645D9;
            font-size: 14px;
            font-weight: 600;
            span{
              color: var(--primary-color);  
            }
          }
          .top-right{
            span{
               color: #1EC7D3;
            font-size: 14px;
            font-weight: 600; 
            }
            img{
              width: 20px; 
              margin-left: 6px; 
            }
          }
        }
        .primary-btn-box{
         margin-top: 10px;
        }
      
      }
        .lock-input{
          border: 1px solid #EEEAF4;
          border-radius: 16px;
          background: #F6F6F6;
          padding: 0 12px 15px;
          margin-top: 20px;
          margin-bottom: 20px;
          .row{
            margin-top: 6px;
          }
        }
    }
}
.pool-address-info{
   padding-left: 12px;
  .info-title{
    font-size: 14px;
    font-weight: 700;
    color: #18184E;
    margin-bottom: 6px;
    
  }
  .pool-address{
    font-size: 14px;
    color: #7A6EAA;
    img{
      width: 15px;
      margin-left: 8px;
    }
  }
}
.countdown-box{
  padding: 15px;
  background: linear-gradient(180deg, #7645D8, #472B7D);
  border-radius: 16px;
  margin-top: 20px;
  .countdown-title{
    font-size: 14px;
    font-weight: 600;
    color: #fff;
  }
  .countdown-text-box{
    background: url(~@/assets/images/time_bg.png) no-repeat;
    background-size: 100% 100%;
    width: 200px;
    margin: 5px auto;
  }
//   .countdown-text{
//     font-size: 20px;
//     font-weight: 600;
//     background: linear-gradient(0deg, #FECD0D 0.146484375%, #FFA849 100%);
//     background-clip: text;
//     height: 46px;
//     line-height: 46px;
//     font-weight: 700;
    
//     text-align: center;
//    -webkit-background-clip: text;
//    -webkit-text-fill-color: transparent;
//   }
  .countdown-tip{
     font-size: 14px;
    color: #fff;
  }
}
.row{
    line-height: 22px;
  .name{
    color: var(--primary-color);
    font-size: 14px;
    font-weight: 600;
  }
  .value{
     color: var(--primary-color);
    font-size: 14px;
    font-weight: 600;
  }
}
.receive-box{
   border: 1px solid #EEEAF4;
   border-radius: 16px;
    padding: 12px;
    margin-bottom: 20px;
    .price-info{
      margin-bottom: 6px;
      .desc{
        color: #280D5F;
